import React, { useState, useEffect } from "react";
import "./App.css";
import logo from './logo.svg';
import {collection} from "./cocktails.json";

import Card from "./Components/Card";

const App = () => {
  console.warn(collection);

  const [cocktailList, setCocktailList] = useState(collection);
  const [filteredList, setFilteredList] = useState([]);
  const [tagFilter, setTagFilter] = useState("");

  useEffect(() => {
    if (!tagFilter.length) {
      setFilteredList(cocktailList);
    } else {
      setFilteredList(cocktailList
        .filter(item => item.tags.join("#").match(tagFilter.replace(/ /g, "#")))
      );
    }
  }, [tagFilter, cocktailList]);

  return (
    <div className="App">
      <h1 className="title">Recettes </h1>
      <input type="text" placeholder="search tags.." onChange={e => setTagFilter(e.target.value)}/>
      <div className="items">
        {filteredList.map((data, i) => <Card key={i} i={i} data={data} />)} 
      </div>
    </div>
  );
};

export default App;