import { useState } from 'react';
import "./Card.css";

const getStyle = (i) => ({
  backgroundColor: ["#B5EAEA", "#F0D9FF", "#FFBCBC", "#F38BA0"][i]
});

const Card = ({ data, i }) => {
const [displayContent, setDisplayContent] = useState(false);

  return (
    <div className="box" style={getStyle(i % 4)} onClick={() => setDisplayContent(!displayContent)}>
      <div className="border"></div>
      {
        !displayContent && (
          <div>
            <span className="tags">{data.tags.map(t => `#${t}`).join(" ")}</span>
            <span className="name">{data.name}</span>
          </div>
        )
      }
      {
        displayContent && (
          <div>
            <span className="name content">{data.name}</span>
            <div className="content">
              {data.description.split("##").map((line, i) => <div key={i}>{line}<br/></div>)}
            </div>
          </div>
        )
      }
    </div>
  );
}

export default Card;